.navbar {
  display: flex;
  padding-inline: 70px;
  height: 70px;
  justify-content: space-between;
  box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.075);
  align-items: center;
  z-index: 1;
  position: relative;
  border-bottom: 0.5px solid #f1f1f1;
}

.navbar svg {
  height: 30px;
  width: 30px;
}

.navbar ul {
  list-style: none;
  display: flex;
  gap: 55px;
}

.navbar li {
  color: #4670B4;
}/*# sourceMappingURL=navbar.css.map */